.App {
  font-family: sans-serif;
  text-align: center;
}
.header {
  display: flex;
  align-items: center;
  height: 120px;
  background-color: #add8e6;
  justify-content: space-between;
}
.header > h1 {
  margin: 0px 40px;
  font-size: 50px;
  font-weight: bold;
  color: #333333;
}
.sidebar {
  display: flex;
  background-color: #aee2ff;
  width: calc(12.5% - 10px);
  min-width: calc(12.5%);
  height: 100%;
  flex-direction: column;
  align-items: center;
  margin: 5px 0px;
  border: #000000 1px solid;
  border-radius: 10px;
}
@media (max-width: 768px) {
  .sidebar {
    flex-direction: row; /* Vertical when screen size is small */
    width: 100%;
  }
  .sidebar > div > button {
    min-width: 10px;
  }
}
.sidebar > div > button {
  padding: 10px;
  background-color: #ffdab9;
  min-width: 150px;
  margin: 10px 0px;
  border-radius: 10px;
  color: #020202;
}
.sidebar > div > button:hover {
  background-color: #a1eebd;
}
.userIcon {
  color: #000000;
  padding-right: 50px;
  font-size: 40px;
}
.userIcon:hover {
  color: #0cb449;
}
.container {
  display: flex;
  flex: 1;
}
.main {
  display: flex;
  flex: 1;
  margin: 5px 10px;
  border-bottom: #000000 5px solid;
  width: 100%;
  height: 100%;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .main {
    
  }
}
.entries {
  display: flex;
  flex: 1;
  justify-content: space-around;
  flex-direction: start;
}
.entry {
  display: flex;
  flex: 1;
  gap: 5px;
  justify-content: center;
  flex-direction: start;
}
.entry > h2,
h1 {
  min-width: 150px;
  margin: 0px;
  padding: 0px;
  margin: 5px;
}
.entry > h1 {
  background-color: brown;
}
.entry > h2 {
  background-color: wheat;
  border: 2px solid;
  border-radius: 10px;
}
.entries > * {
  border: 5px solid;
  width: 100%;
  flex-basis: 0;
  height: 100%;
  padding: 20px;
}
.entries > div > * {
  margin: 0px;
  padding: 0px;
}
.App1 {
  display: flex;
  justify-content: space-around;
   width: 100%;
  
}
.entry {
  display: flex;
  width: 100%;
}
.entry > form {
  border: 2px solid black;
  margin: 1px;
  border-radius: 5px;
  background-color: burlywood;
  width: inherit;
}
.entry > form > input {
  display: inline-block;
  width: 90px;
  padding-left: 10px;
  justify-content: center;
}
.entry > form > input[type="submit"] {
  background-color: #affac7;
}
.morning {
  display: flex;
  background-color: #e5f04f;
  justify-content: center;
  width: 100%;
}
.evening {
  display: flex;
  justify-content: center;
  background-color: #e5f04f;
  width: 100%;
}
.one {
  background-color: #e5f04f;
  width: 100%;
  border-right: 5px solid;
  border: 2px solid;
}
.two {
  background-color: #e5f04f;
  width: 100%;
  border: 2px solid;
}

.two > h2 {
  display: inline-block;
  margin-left: 20px;
}
.one > h2 {
  display: inline-block;
  margin-left: 20px;
}



.morningEntries {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border: 2px solid;
  border-collapse: collapse;
  width: 100%;
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.MorningHeading {
  padding: 5px;
  background-color: rgb(240, 158, 219);
  color: white;
  border-bottom: 1px black solid;
}
.MorningData {
  padding: 5px;
  border-bottom: 2px solid;
}
.MorningData:hover {
  background-color: aquamarine;
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.MorningHeading:hover {
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.ENTRIES{
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: inherit;
  gap :10px;
}
.ENTRIES>div{
  justify-content: center;
}
.green-row {
  background-color: green;
}
